import { Link } from "gatsby"
import React from "react"
import { Path } from "../../helpers/constants"
import Icon from "../Icon"
import { IconType } from "../Icon/style"
import Nav from "./nav"
import * as Styled from "./style"

export interface HeaderProps {
  onClickHamburgerButton?: () => void
}

export default function Header({ onClickHamburgerButton }: HeaderProps) {
  return (
    <Styled.Header>
      <HamburgerButton onClick={() => onClickHamburgerButton?.()} />
      <Styled.SiteTitle>
        <Styled.SiteLogo>
          <Link to={Path.Home}></Link>
        </Styled.SiteLogo>
      </Styled.SiteTitle>
      <Styled.NavList></Styled.NavList>
      <Styled.Contacts>
        <Nav to="/feed.xml">
          <Icon type={IconType.RSS} style={{ filter: "invert(0.6)" }} />
        </Nav>
        <Nav to={`https://github.com/jeonghwan-kim`}>
          <Icon type={IconType.Github} style={{ filter: "invert(0.6)" }} />
        </Nav>
      </Styled.Contacts>
    </Styled.Header>
  )
}

function HamburgerButton({ onClick }: { onClick: () => void }) {
  return (
    <Styled.HamburgerButton onClick={onClick}>
      <span />
      <span />
      <span />
    </Styled.HamburgerButton>
  )
}
